import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'
import './uni.promisify.adaptor'
import store from './store/index'
import '@/static/iconfont/iconfont.css'

Vue.config.productionTip = false

const toast = (title,icon)=>{
	uni.showToast({
		title,
		icon,
		duration:2000
	})
}

Vue.prototype.$toast = toast

App.mpType = 'app'
const app = new Vue({
  ...App,
	store
})
app.$mount()